import React, { useState } from "react";
import ReviewCard from "../ReviewCard/ReviewCard";
import "./Reviews.css";

import user1 from "../../assets/images/rev1.png";
import user2 from "../../assets/images/rev2.png";
import user3 from "../../assets/images/rev1.png";
import user4 from "../../assets/images/rev2.png";

const Reviews = () => {
  const [activeSlide, setActiveSlide] = useState(0);

  const reviewsData = [
    {
      id: 1,
      author: " ",
      text: "                / ,       .",
      photo: user1,
    },
    {
      id: 2,
      author: " ",
      text: "       /    3       -  .",
      photo: user2,
    },
    {
      id: 3,
      author: " ",
      text: " !      .   .",
      photo: user3,
    },
    {
      id: 4,
      author: " ",
      text: "  .  ,     .   !",
      photo: user4,
    },
  ];

  const slides = [];
  for (let i = 0; i < reviewsData.length; i += 2) {
    slides.push(reviewsData.slice(i, i + 2));
  }

  const handleDotClick = (index) => {
    setActiveSlide(index);
  };

  return (
    <section className="reviews-section" id="">
      <div className="reviews-container">
        <h2 className="reviews-title"></h2>

        <div className="reviews-grid">
          {slides[activeSlide]?.map((review) => (
            <ReviewCard
              key={review.id}
              author={review.author}
              text={review.text}
              photo={review.photo}
            />
          ))}
        </div>

        <div className="reviews-pagination">
          <div className="pagination-dots">
            {slides.map((_, index) => (
              <span
                key={index}
                className={`dot ${index === activeSlide ? "active" : ""}`}
                onClick={() => handleDotClick(index)}
              />
            ))}
          </div>
        </div>
      </div>
    </section>
  );
};

export default Reviews;
